'use client';

import { useEffect, useState } from 'react';
import { useRouter } from 'next/navigation';
import { lists } from '@/api';
import { wsService } from '@/services/websocket';
import ListCard from '@/components/ListCard';
import CreateListModal from '@/components/CreateListModal';
import RecipesPage from '../recipes/page';

interface List {
    id: number;
    title: string;
    description: string;
    createdAt: string;
}

type DashboardView = 'lists' | 'recipes';

export default function DashboardPage() {
    const router = useRouter();
    const [currentView, setCurrentView] = useState<DashboardView>('lists');
    const [userLists, setUserLists] = useState<List[]>([]);
    const [loading, setLoading] = useState(true);
    const [showModal, setShowModal] = useState(false);
    const [error, setError] = useState('');
    const [isClient, setIsClient] = useState(false);

    useEffect(() => {
        setIsClient(true);
    }, []);

    useEffect(() => {
        if (!isClient) return;

        checkAuth();
        fetchLists();

        // WebSocket verbinden f√ºr Live-Updates
        const token = localStorage.getItem('token');
        if (token) {
            wsService.connect(token).catch(err => {
                console.error('WebSocket-Verbindung fehlgeschlagen:', err);
            });

            // Abonniere List-Updates
            const unsubscribe = wsService.subscribe('list_updated', () => {
                console.log('Liste aktualisiert - neu laden');
                fetchLists();
            });

            return () => {
                unsubscribe();
                wsService.disconnect();
            };
        }
    },[isClient]);

    const checkAuth = () => {
        const token = localStorage.getItem('token');
        if (!token) {
            router.push('/login');
        }
    };

    const fetchLists = async () => {
        try {
            setLoading(true);
            const data = await lists.getAll();
            setUserLists(data);
        } catch (err) {
            console.error(err);
        } finally {
            setLoading(false);
        }
    };

    const handleCreateList = async (title: string, description: string) => {
        try {
            await lists.create(title,description);
            setShowModal(false);
            fetchLists();
        } catch (err) {
            setError(err instanceof Error ? err.message : 'Fehler beim Erstellen der Liste');
        }
    };

    const handleDeleteList = async (id: number) => {
        if (confirm('Liste wirklich L√∂schen?')) {
            try {
                await lists.delete(id);
                fetchLists();
            } catch (err) {
                setError(err instanceof Error ? err.message : 'Fehler beim L√∂schen der Liste')
            }
        } 
    };

    return (
        <div className="min-h-screen bg-[#0a0a0f] flex flex-col">
        {/* Header */}
        <header className="bg-gradient-to-b from-[#14141f] to-transparent border-b border-[#2d2d3f]/50">
            <div className="max-w-6xl mx-auto px-3 sm:px-6 py-4 sm:py-6">
                <div className="flex justify-between items-center gap-2 sm:gap-4">
                    <div className="flex items-center gap-2 sm:gap-3 min-w-0">
                        <div className="flex-shrink-0">
                            <img src="/FoodPlaner.png" alt="FoodPlaner Logo" className="w-10 h-10 sm:w-12 sm:h-12 rounded-2xl" />
                        </div>
                        <div className="text-left min-w-0">
                            <h1 className="text-xl sm:text-3xl font-bold bg-gradient-to-r from-white to-gray-300 bg-clip-text text-transparent truncate">FoodPlaner</h1>
                            <p className="text-xs text-gray-500 hidden sm:block">Einkaufslisten verwalten</p>
                        </div>
                    </div>
                    <div className="flex gap-1 sm:gap-2 flex-shrink-0">
                        <button
                            onClick={() => setCurrentView('lists')}
                            className={`px-2 sm:px-4 py-2 rounded-lg sm:rounded-xl text-xs sm:text-sm font-medium transition-all whitespace-nowrap ${
                                currentView === 'lists'
                                    ? 'bg-purple-500 text-white'
                                    : 'bg-[#1a1a2e]/80 border border-[#2d2d3f] text-gray-400 hover:text-purple-400'
                            }`}
                            title="Listen"
                        >
                            <span className="hidden sm:inline">Listen</span>
                            <span className="sm:hidden">üìã</span>
                        </button>
                        <button
                            onClick={() => setCurrentView('recipes')}
                            className={`px-2 sm:px-4 py-2 rounded-lg sm:rounded-xl text-xs sm:text-sm font-medium transition-all whitespace-nowrap ${
                                currentView === 'recipes'
                                    ? 'bg-purple-500 text-white'
                                    : 'bg-[#1a1a2e]/80 border border-[#2d2d3f] text-gray-400 hover:text-purple-400'
                            }`}
                            title="Rezepte"
                        >
                            <span className="hidden sm:inline">Rezepte</span>
                            <span className="sm:hidden">üç≥</span>
                        </button>
                        <button
                            onClick={() => router.push('/profile')}
                            className="p-2 sm:p-3 bg-[#1a1a2e]/80 border border-[#2d2d3f] hover:border-purple-500/50 rounded-lg sm:rounded-xl transition-all text-gray-400 hover:text-purple-400 flex-shrink-0"
                            title="Profil"
                        >
                            <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M16 7a4 4 0 11-8 0 4 4 0 018 0zM12 14a7 7 0 00-7 7h14a7 7 0 00-7-7z" />
                            </svg>
                        </button>
                    </div>
                </div>
            </div>
        </header>

        {/* Main Content */}
        <main className="flex-1 flex flex-col max-w-6xl mx-auto px-3 sm:px-6 py-4 sm:py-12 w-full">
            <div className="w-full">
            {error && (
            <div className="bg-red-500/10 border border-red-500/30 text-red-400 px-3 sm:px-4 py-2 sm:py-3 rounded-xl sm:rounded-2xl mb-4 sm:mb-8 flex items-center justify-between backdrop-blur-sm">
                <span className="text-xs sm:text-sm">{error}</span>
                <button
                onClick={() => setError('')}
                className="text-red-400 hover:text-red-300 font-bold ml-2 flex-shrink-0"
                >
                ‚úï
                </button>
            </div>
            )}

            {/* Lists View */}
            {currentView === 'lists' && (
                <div className="w-full flex flex-col items-center justify-center">
                    {loading ? (
                    <div className="text-center py-16 sm:py-32">
                        <div className="inline-block w-12 sm:w-16 h-12 sm:h-16 border-4 border-purple-500/20 border-t-purple-500 rounded-full animate-spin mb-3 sm:mb-4"></div>
                        <p className="text-xs sm:text-base text-gray-400">L√§dt deine Listen...</p>
                    </div>
                    ) : userLists.length === 0 ? (
                    <div className="text-center py-12 sm:py-20">
                        <h2 className="text-lg sm:text-2xl font-bold text-white mb-2">Keine Listen vorhanden</h2>
                        <p className="text-xs sm:text-base text-gray-400 mb-6 sm:mb-8">Erstelle deine erste Einkaufsliste</p>
                        <button
                            onClick={() => setShowModal(true)}
                            className="inline-flex items-center gap-2 sm:gap-3 bg-gradient-to-r from-purple-500 to-purple-600 hover:from-purple-600 hover:to-purple-700 text-white font-bold py-3 sm:py-4 px-6 sm:px-8 rounded-xl sm:rounded-2xl transition-all shadow-xl shadow-purple-500/40 hover:shadow-purple-500/60 hover:scale-[1.03] active:scale-95 text-sm sm:text-base"
                        >
                            <svg className="w-5 h-5 sm:w-6 sm:h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 4v16m8-8H4" />
                            </svg>
                            <span className="hidden sm:inline">Erste Liste erstellen</span>
                            <span className="sm:hidden">Liste erstellen</span>
                        </button>
                    </div>
                    ) : (
                    <div className="w-full">
                        <div className="flex justify-center items-center mb-6 sm:mb-8 flex-col gap-3 sm:gap-4">
                            <div className="text-center">
                                <h2 className="text-lg sm:text-xl font-bold text-white">Deine Listen</h2>
                                <p className="text-xs sm:text-sm text-gray-400 mt-1">{userLists.length} {userLists.length === 1 ? 'Liste' : 'Listen'}</p>
                            </div>
                            <button
                                onClick={() => setShowModal(true)}
                                className="inline-flex items-center gap-1 sm:gap-2 bg-gradient-to-r from-purple-500 to-purple-600 hover:from-purple-600 hover:to-purple-700 text-white font-bold py-2 sm:py-3 px-4 sm:px-6 rounded-lg sm:rounded-2xl transition-all shadow-lg shadow-purple-500/40 hover:shadow-purple-500/60 hover:scale-[1.02] active:scale-95 text-xs sm:text-base"
                            >
                                <svg className="w-4 h-4 sm:w-5 sm:h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 4v16m8-8H4" />
                                </svg>
                                <span className="hidden sm:inline">Neue Liste</span>
                                <span className="sm:hidden">Neue</span>
                            </button>
                        </div>
                        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-3 sm:gap-5 justify-items-center max-w-5xl mx-auto w-full">
                            {userLists.map((list) => (
                            <ListCard
                                key={list.id}
                                list={list}
                                onDelete={handleDeleteList}
                            />
                            ))}
                        </div>
                    </div>
                    )}
                </div>
            )}

            {/* Recipes View */}
            {currentView === 'recipes' && (
                <RecipesPage shoppingLists={userLists} />
            )}
            </div>
        </main>

        {/* Modal */}
        {showModal && (
            <CreateListModal
            onClose={() => setShowModal(false)}
            onCreate={handleCreateList}
            />
        )}
        </div>
    );
}
